import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, Div, View, Group, Form, Card, Root,InfoRow,List,Cell,Link,PullToRefresh,Button} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import ReactDOM from 'react-dom'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

export default class Persik extends React.Component {

	constructor(props){
	super(props);

	this.state={
		title:"",
		text:"",
		mesto:"",
		stattime:"",
		owner_id:"",
		chatlink:"",
		type:"",
		igo:0,
		bookmark:0,
		}


	}

	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	igo(){
		if (this.state.igo==1){
			this.setState({igo:0})
		}else{
			this.setState({igo:1})

		}
	}
	editbookmark(){
		if (this.state.bookmark==1){
			this.setState({bookmark:0})
		}else{
			this.setState({bookmark:1})

		}
	}
	buttons(){

			if(this.state.igo==1){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} level="outline" onClick={()=>this.igo()}>Не пойду</Button>
						<Button size="l" onClick={()=>this.editbookmark()} stretched level="secondary">{this.state.bookmark ==1? 'Убрать из закладок' : 'Добавить в закладки' }</Button>
					</Div>
				);
			}else if(this.state.igo==0){
				return(
					<Div style={{display: 'flex'}}>
						<Button size="l" stretched style={{ marginRight: 8 }} onClick={()=>this.igo()}>Я пойду</Button>
						<Button size="l" onClick={()=>this.editbookmark()} stretched level="secondary">{this.state.bookmark ==1? 'Убрать из закладок' : 'Добавить в закладки' }</Button>
					</Div>
				);
			}

	}

	getOwner(){
		const {title,text,go,mesto,starttime,type,owner_id}=this.props;

		var own="https://vk.com/id"+owner_id.toString();
		if(owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}


	render() {

		const {title,text,go,mesto,starttime,type,owner_id}=this.props;

		return (


				<Panel id="full">
					<PanelHeader
						left={<HeaderButton onClick={go} data-to="home">{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}</HeaderButton>}>
						Событие
					</PanelHeader>

					<Group><Div>{title}</Div></Group>
					{this.buttons()}

					<Group>
						<Div>
							<InfoRow title="Примечание">
								{text}
							</InfoRow>
						</Div>
					</Group>
					<Group>
						<List>
							<Cell>
								<InfoRow title="Место">
									{mesto}
								</InfoRow>
							</Cell>
							<Cell>
								<InfoRow title="Начало">
									{starttime}
								</InfoRow>
							</Cell>
							<Cell>
								<InfoRow title="Тип">
									{type}
								</InfoRow>
							</Cell>
							{this.getOwner()}
						</List>
					</Group></Panel>


		);
	}
}
const style={
	fd: {

		width: '100%',
		display: 'flex',
		height: '100%',
		justifyContent: 'bottom',


	},
	buttons:{
		height: '100%',
		padding: '3px',
		width: '100%',


	},
};
Persik.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};


