import React from 'react';
import PropTypes from 'prop-types';
import {
	Panel,
	ListItem,
	Button,
	Group,
	Div,
	Avatar,
	PanelHeader,
	View,
	Tabbar,
	TabbarItem,
	Epic,
	Card,

	Root,
	HeaderButton, IOS, InfoRow, List, Cell, platform, Link
} from '@vkontakte/vkui';

import connect from "@vkontakte/vkui-connect";
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon28Newsfeed from '@vkontakte/icons/dist/28/newsfeed';
import Icon28Search from '@vkontakte/icons/dist/28/search';
import Icon28Messages from '@vkontakte/icons/dist/28/messages';
import Icon28Notifications from '@vkontakte/icons/dist/28/notifications';
import Icon28More from '@vkontakte/icons/dist/28/more';

import Persik from './Persik';
import ReactDOM from "react-dom";
const osname = platform();

const Home = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
		<PanelHeader>Example</PanelHeader>
		{fetchedUser &&
		<Group title="User Data Fetched with VK Connect">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
		</Group>}

		<Group title="Navigation Example">
			<Div>
				<Button size="xl" level="2" onClick={go} data-to="persik">
					Show me the Persik, please
				</Button>
			</Div>
		</Group>
	</Panel>
);
class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'persik',
			fetchedUser: null,
			activeStory: 'feed',
			events:[],
			activeView:'home',
			title:"",
			text:"",
			mesto:"",
			stattime:"",
			owner_id:"",
			chatlink:"",
			type:"",
		};
	}
	back(i){
		this.setState({activeView:"home"})
	}
	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	to(panel){
		const { id, go, act } =this.props;

		this.setState({act:"full"})
	}
	componentDidMount() {
		this.load()
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}
	onStoryChange =(e)=>{
		this.setState({ activeStory: e.currentTarget.dataset.story })
	}
	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	goo(i){
		var title=decodeURIComponent(this.state.events[i].split("&&&")[0])
		var mesto=decodeURIComponent(this.state.events[i].split("&&&")[1]).split("+").join(" ")
		var datetime=decodeURIComponent(this.state.events[i].split("&&&")[2])
		var chatlink=decodeURIComponent(this.state.events[i].split("&&&")[3])
		var owner_id=decodeURIComponent(this.state.events[i].split("&&&")[4])
		var text=decodeURIComponent(this.state.events[i].split("&&&")[5]).split("+").join(" ")
		var type=decodeURIComponent(this.state.events[i].split("&&&")[6])
		this.setState({activeView:"full",title:title,text:text,mesto:mesto,starttime:datetime,owner_id:owner_id,chatlink:chatlink,type:type});
	}
	load(){


		if(this.state.events.length==0){
			var exp=""
			var re=this.get("https://api.finebot.site/events/events.get")
			re=JSON.parse(re)
			var i =0
			for(i;i<re.response.length;i++){

				var mesto=re.response[i].country+", "+re.response[i].city+", "+re.response[i].street
				this.state.events.push(encodeURIComponent(re.response[i].title)+"&&&"+encodeURIComponent(mesto)+"&&&"+encodeURIComponent(re.response[i].starttime)+"&&&"+encodeURIComponent(re.response[i].chatlink)+"&&&"+encodeURIComponent(re.response[i].owner_id)+"&&&"+encodeURIComponent(re.response[i].text)+"&&&"+encodeURIComponent(re.response[i].type))


			}
		}
		this.setState({fetching:false})
		//document.getElementById("test").
	}
	getOwner(){
		var own="https://vk.com/id"+this.state.owner_id.toString();
		if(this.state.owner_id.toString()==="undefined"){
			return ""
		}else{
			return (<Cell>
				<InfoRow title="Организатор">
					<Link href={own}>vk.com/id{this.state.owner_id}</Link>
				</InfoRow>
			</Cell>);
		}
	}
	ret(item, i){

		var title=decodeURIComponent(item.split("&&&")[0])
		var mesto=decodeURIComponent(item.split("&&&")[1])
		var datetime=decodeURIComponent(item.split("&&&")[2])
		const {go}=this.props
		return(

			<Group  style={{height:'100%'}}>
				<Div>
					<form style={{textAlign:'down',width:'100%',marginBottom:5}} >{title}
					</form>
					<form style={{fontSize:12, textAlign:'top',marginBottom:15}}>{decodeURIComponent(mesto).split("+").join(" ")}<br/>{datetime}
					</form>
					<Button onClick={go} data-to="full">Перейти</Button>
				</Div>


			</Group>

		);
	}
	get(url){
		var xhr = new XMLHttpRequest();
		xhr.open('POST', url, false);
		xhr.send();
		return xhr.responseText;
	}
	render() {
		const { id, go, act } =this.props;
		return (

			<Panel id="home">
				<PanelHeader
				>Events</PanelHeader>
				{this.state.events.map(this.ret.bind(this))}

			</Panel>
		);
	}
}

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default App;
